import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Toast } from "vant";
import { Action } from "vuex-class";

import { Cell, CellGroup, ImagePreview } from "vant";
import Wintabe from "../../components/Wintabe.vue";
import Winbeet from "../../components/Winbeet.vue";
@Component({
  components: {
    Wintabe,
    Winbeet
  },
  mixins: [mixin]
})
export default class ProductDetail extends Vue {
  mounted() {
    (this.catId = sessionStorage.catId),
      (this.parentId = sessionStorage.parentId);
    this.getSecCatList();
  }

  parentId = "";
  changeRed = "0";

  goProductDetail(goodsId) {
    this.$router.push({
      path: "/productdetail",
      query: {
        goodsId: goodsId
      }
    });
  }
  getSecCatList() {
    //二级菜单
    Vue.prototype.$reqFormPost1(
      "/user/cat/list",
      {
        parentId: this.parentId
      },
      res => {
        if (res.returnCode !== 200) {
          this["$Message"].warning(res.message);
          console.log(res.message);
          return;
        }
        this.catList = res.data;

        let a = this.catList.filter((item, index) => {
          return item.catId == this.catId;
        });
        this.checkSecCat(a[0]);
      }
    );
  }

  checkSecCat(item) {
    this.secItem = item;
    this.catId = item.catId;
    this.getproductList();
  }
  sortStatus: any = false;
  doPriceFitler(sortName) {

    if (this.sortName !== sortName) {
      this.sortName = sortName;
    } else {
        this.sortStatus= !this.sortStatus
    }
    this.getproductList();
  }

  getproductList() {
    console.log(this.sortName)
    let data = {
      catId: this.catId
    };
    if ((this.sortName || "") !== "") {
      (<any>Object).assign(data, { sortName: this.sortName });
    }
    if (this.sortStatus  !='' || this.sortStatus  !=undefined) {
      (<any>Object).assign(data, { sortStatus: this.sortStatus });
    }

    Vue.prototype.$reqFormPost1("/user/goods/list", data, res => {
      if (res.returnCode !== 200) {
        this["$Message"].warning(res.message);
        console.log(res.message);
        return;
      }
      this.shopList = res.data.goodsList;
      console.log(this.shopList)
    });
  }

  secItem = {};

  catId = "";
  catList = [];
  sortName = "";

  shopList = [];
  goodsId = "";
  detatil: any = {
    commentList: [],
    detail: {
      imageList: []
    },
    goodsImg: "",
    goodsName: "",
    jingle: "",
    labelPrice: 0,
    marketPrice: 0,
    onlineStatus: "",
    sku: [],
    skuKey: [],
    storageNum: 0
  };
  num = 1;
  skuattr = [];
  chosenList = [];
  chosensku = [];
  skuItem = {};
  commentnum = 0;
  praise = "0";
  tabindex = 0;
  handleStatus(type){
    if(this.sortName==type && this.sortStatus == false){
      return 'sortPrice'
    } else if(this.sortName==type && this.sortStatus){
      return 'sortTime';
    }else{
       return ''
    }
 
 }
  selecttablist(index) {
    this.tabgoodslist = [];
    if (index == 0) {
      this.tabgoodslist = this.likeList;
      this.tabindex = 0;
    }
    if (index == 1) {
      this.tabgoodslist = this.newList;
      this.tabindex = 1;
    }
  }

  tabgoodslist = [];
  likeList = [];
  newList = [];
}



